import Base from '../common/Base';
export default class License extends Base {
  constructor(props) {
    super(props, {
      platform: 'github',
      type: 'license'
    });
    this.getUrl = () => {
      var {
        type,
        platform,
        user,
        repo,
        base
      } = this.state;
      if (platform !== 'github') return '';
      return [base, platform, type, user, repo].join('/');
    };
  }
}